.explore {
  @media (min-width: 744px) {
    display: grid;
    grid-template-rows: 1fr;
  }

  @media (min-width: 1260px) {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 24px;
      align-self: stretch;
    }

   &__heading {
    font-family: Cinzel, sans-serif;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: center;
    margin-top: 100px;
    margin-bottom: 40px;

    @media (min-width: 744px) {
      font-size: 48px;
      line-height: 48px;
    }
    }


  &__item {
    height: 100%;
    width: 100%;
    object-fit: cover;
   }

  &__content {
    font-family: Roboto, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0;
    text-align: center;
    padding-top: 24px;
    padding-bottom: 8px;

      &-hightlight {
    font-family: Cinzel, sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    letter-spacing: 0;
    text-align: center;
    color: #D12D35;
  }
  }

 }
