
.button {
  &__first {
    background: #D12D35;
    display: block;
    text-decoration: none;
    text-align: center;
    height: 56px;
    line-height: 56px;
    font-family: Cinzel, sans-serif;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0;
    color: #FFF;
    justify-content: center;
  }


  &__second {
    font-family: Cinzel, sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0;
    text-align: center;
    color: #FFF;
    background: #D12D35;
    width: 280px;
    height: 56px;
    top: 4930px;
    left: 20px;
    border: 0;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background: #AE1C23;

       &:active {
      background: #EB343D;
    }
    }
  }
}
